@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.description {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  font-size: 14px;
  line-height: 21px;
}
.title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
  }
}
.button {
  padding: 0 50px;
  border: none;
  font-family: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 58px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #81b3d2;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
  cursor: not-allowed;
}
.button:focus-visible {
  outline: 2px solid #5496bd;
}
.advantages-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.advantages-item--background-blue {
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantages-item--background-blue {
    display: grid;
    grid-column: span 3;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advantages-item--background-gray {
  background-color: #eeeeee;
  padding: 24px 14px;
}
@media (max-width: 767px) {
  .advantages-item--background-gray {
    padding: 0 18px;
  }
}
@media (min-width: 768px) {
  .advantages-item--background-gray {
    padding: 46px 0;
    justify-content: flex-start;
  }
}
.advantages-item__wrapper {
  display: flex;
  flex-direction: column;
  padding: 35px 20px;
  justify-content: center;
}
@media (min-width: 768px) {
  .advantages-item__wrapper {
    padding: 0 40px;
  }
}
.advantages-item__wrapper--bordered {
  border-bottom: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .advantages-item__wrapper--bordered {
    border-bottom: none;
  }
}
.advantages-item__text br {
  display: none;
}
.advantages-item__text--white {
  color: #ffffff;
}
.advantages-item__text--no-tablet-break br {
  display: block;
}
@media (min-width: 768px) and (max-width: 1199px) {
  .advantages-item__text--no-tablet-break br {
    display: none;
  }
}
.advantages-item__text--tablet-break {
  padding: 0 10px;
}
@media (min-width: 768px) {
  .advantages-item__text--tablet-break {
    padding: 0 20px;
  }
  .advantages-item__text--tablet-break br {
    display: block;
  }
}
@media (min-width: 1200px) {
  .advantages-item__text--desktop-break br {
    display: block;
  }
}
.advantages-item__features {
  display: grid;
  padding: 25px 20px 45px;
  row-gap: 30px;
  grid-template-columns: 1fr;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .advantages-item__features {
    grid-column: span 3;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
}
.advantages-item__features-item__wrapper {
  display: grid;
  justify-items: start;
  grid-template-rows: auto 1fr;
}
@media (min-width: 768px) {
  .advantages-item__features-item__wrapper {
    justify-items: center;
  }
}
.advantages-item__features-item {
  margin: 0;
  text-transform: uppercase;
  text-align: left;
  display: grid;
  height: 100%;
  column-gap: 8px;
  grid-auto-flow: column;
  padding: 0;
  align-self: start;
  justify-content: start;
}
.advantages-item__features-item::before {
  display: block;
  width: 40px;
  height: 40px;
  background-position: left;
  background-size: 40px 40px;
  background-repeat: no-repeat;
  content: "";
}
.advantages-item__features-item--apartments::before {
  background-image: url("../img/apartments.svg");
}
.advantages-item__features-item--food::before {
  background-image: url("../img/food.svg");
}
.advantages-item__features-item--souvenirs::before {
  margin-right: -5px;
  background-image: url("../img/souvenirs.svg");
}
@media (min-width: 768px) {
  .advantages-item__features-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 32px;
  }
  .advantages-item__features-item::before {
    margin: 0;
    height: 75px;
    width: 76px;
    background-size: 76px 75px;
  }
}
.advantages-item__features-title {
  margin-top: 10px;
  margin-bottom: 16px;
}
.advantages-item__features-text {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: left;
  font-size: 14px;
}
@media (min-width: 768px) {
  .advantages-item__features-text {
    text-align: center;
    max-width: 282px;
  }
}
.advantages-item__image {
  padding: 0;
}
@media (min-width: 768px) {
  .advantages-item__image {
    grid-column: span 2;
  }
}
@media (min-width: 768px) {
  .advantages-item__image--bridge {
    grid-row-start: 1;
  }
}
.advantages {
  display: flex;
  flex-direction: column;
}
.advantages__header {
  max-width: 520px;
  align-self: center;
  padding: 32px 24px 16px;
}
@media (min-width: 768px) {
  .advantages__header {
    padding-right: 0;
    padding-left: 0;
    padding-bottom: 56px;
  }
}
@media (min-width: 1200px) {
  .advantages__header {
    padding-left: 18px;
    padding-right: 18px;
  }
}
.advantages__header-title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 35px;
}
.advantages__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advantages__image {
  display: block;
  width: 100%;
  object-fit: cover;
}
@media (max-width: 767px) {
  .advantages__item--triangle {
    padding-top: 72px;
    background-image: url("../img/down-directed-triangle.svg");
    background-position: top;
    background-size: contain;
    background-repeat: no-repeat;
  }
}
.advantages__title br {
  display: none;
}
@media (min-width: 768px) {
  .advantages__title br {
    display: block;
  }
}
.advantages__number {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  margin: 12px auto 16px;
}
.booking__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 18px 50px;
}
@media (min-width: 768px) {
  .booking__wrapper {
