  }
}
.feedback-form {
  padding-bottom: 34px;
}
@media (min-width: 768px) {
  .feedback-form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 88px;
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .feedback-form {
    padding: 0 120px;
  }
}
.feedback-form__fieldset {
  display: flex;
  flex-direction: column;
  padding: 0 20px 15px;
  margin: 0;
  margin-bottom: 40px;
  border: none;
}
.feedback-form__fieldset:not(:last-of-type) {
  border-bottom: 1px solid #e5e5e5;
}
@media (min-width: 768px) {
  .feedback-form__fieldset:not(:last-of-type) {
    border-bottom: none;
  }
}
@media (min-width: 768px) {
  .feedback-form__fieldset {
    padding: 0;
    border: none;
  }
}
.feedback-form__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .feedback-form__wrapper {
    display: grid;
    row-gap: 16px;
  }
}
@media (min-width: 768px) {
  .feedback-form__fieldset-reviewer {
    padding-right: 44px;
    padding-bottom: 20px;
    margin-right: -44px;
    border-right: 1px solid #d8d8d8;
  }
}
@media (min-width: 768px) {
  .feedback-form__fieldset-contacts {
    padding-bottom: 20px;
  }
}
.feedback-form__fieldset-impression {
  grid-column: span 2;
}
@media (min-width: 768px) {
  .feedback-form__fieldset-impression {
    border-top: 1px solid #d8d8d8;
  }
}
@media (min-width: 768px) {
  .feedback-form__wrapper--impression {
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    column-gap: 50px;
    row-gap: 16px;
  }
}
@media (min-width: 1200px) {
  .feedback-form__wrapper--impression {
    grid-template-columns: repeat(3, 1fr);
    padding: 40px 0 60px;
  }
}
@media (min-width: 768px) {
  .feedback-form__wrapper--places {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    margin: 0 auto;
    column-gap: 100px;
    row-gap: 16px;
  }
}
@media (min-width: 1200px) {
  .feedback-form__wrapper--places {
    grid-template-columns: repeat(4, 1fr);
    padding: 40px 0 60px;
  }
}
.feedback-form__fieldset-places {
  grid-column: span 2;
}
@media (min-width: 768px) {
  .feedback-form__fieldset-places {
    border-top: 1px solid #d8d8d8;
  }
}
.feedback-form__fieldset-freetext {
  grid-column: span 2;
}
@media (min-width: 768px) {
  .feedback-form__fieldset-freetext {
    border-top: 1px solid #d8d8d8;
  }
}
@media (min-width: 1200px) {
  .feedback-form__fieldset-freetext {
    padding: 0 96px;
  }
}
.feedback-form__fieldset-name {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .feedback-form__fieldset-name {
    font-size: 21px;
  }
  .feedback-form__fieldset-name--bordered {
    padding: 0 50px;
  }
}
.feedback-form__item-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.feedback-form__item-wrapper:not(:last-child) {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .feedback-form__item-wrapper:not(:last-child) {
    margin-bottom: 0;
  }
}
.feedback-form__item-wrapper:focus-within .form-item__input {
  outline: 2px solid #81b3d2;
  -webkit-outline: 2px solid #81b3d2;
}
.feedback-form__button-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  align-items: center;
}
@media (min-width: 768px) {
  .feedback-form__button-wrapper {
    display: grid;
    padding: 0;
    grid-template-columns: repeat(2, 1fr);
    grid-column: span 2;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .feedback-form__button-wrapper {
    padding: 0 94px;
  }
}
.feedback-form__submit {
  padding: 0 48px;
  align-self: stretch;
}
.feedback-form__explanation {
  display: block;
  margin-left: auto;
  font-size: 12px;
  line-height: 21px;
  color: rgba(51, 51, 51, 0.6);
}
@media (min-width: 768px) {
  .feedback-form__explanation {
    align-self: unset;
    justify-self: end;
  }
}
.form-item {
  position: relative;
}
.form-item--radio {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
.form-item--checkbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
.form-item__name {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: center;
  color: #333333;
  margin-left: 16px;
  font-size: 14px;
  line-height: 21px;
}
.form-item__name--bold {
  text-transform: inherit;
  font-weight: 700;
  font-size: 18px;
}
.form-item__input {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  text-align: left;
  padding: 0 20px;
  border: none;
  border-radius: 0;
  align-self: stretch;
  text-transform: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 58px;
  background-color: #f2f2f2;
  outline: none;
}
.form-item__input:invalid {
  outline: 2px solid #d60303;
}
.form-item__input::placeholder {
  text-transform: none;
  line-height: 26px;
  margin: 0;
  color: rgba(0, 0, 0, 0.3);
}
.form-item:hover .form-item__input {
  background-color: #e5e5e5;
}
.form-item:active .form-item__input {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
}
.form-item__input-radio {
  position: relative;
  padding: 0;
  width: 46px;
  height: 46px;
  border-radius: 50%;
}
.form-item:active .form-item__input-radio {
  background-color: #d5d5d5;
  outline: none;
}
.form-item--radio:active .radio:checked + .form-item__input::after {
  background-color: rgba(51, 51, 51, 0.3);
}
.form-item__icon {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #e6e6e6;
  padding: 17.5px 20px;
}
.form-item__input-checkbox {
  padding: 23px;
  border-radius: 4px;
  width: auto;
}
.checkbox:checked + .form-item__input {
  background-position: center;
  background-size: 22px 16px;
  background-repeat: no-repeat;
  background-image: url("../img/checkbox-tick.svg");
}
.form-item:active .form-item__input-checkbox {
  background-color: #d5d5d5;
  outline: none;
}
.form-item--checkbox:active .checkbox:checked + .form-item__input {
  background-image: url("../img/checkbox-tick-active.svg");
}
.form-item__input-textarea {
  font-family: "PT Sans", sans-serif;
  padding: 20px;
  line-height: 26px;
  resize: vertical;
}
.footer__wrapper {
