.navigation-toggle {
  display: flex;
  flex-direction: column;
  height: 22px;
  padding: 0;
  background-color: transparent;
  border: none;
  align-items: center;
  justify-content: center;
}
.navigation-toggle:hover {
  opacity: 0.6;
}
.navigation-toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation-toggle {
    display: none;
  }
}
.navigation-toggle--nojs {
  display: none;
}
.navigation-toggle__line {
  display: block;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transition: 0.3s;
}
.navigation-toggle__line:not(:last-of-type) {
  margin-bottom: 5px;
}
.navigation-toggle__line-first--open {
  transform: rotate(45deg) translate3d(8px, 5px, 0);
  background-color: #ffffff;
}
.navigation-toggle__line-second--open {
  transform: scale(0);
}
.navigation-toggle__line-third--open {
  transform: rotate(-45deg) translate3d(8px, -5px, 0);
  background-color: #ffffff;
}
.navigation {
  background-color: #ffffff;
}
.navigation__toggle {
  width: 56px;
  height: 56px;
  margin-left: auto;
  margin-right: 4px;
  z-index: 10;
  cursor: pointer;
}
.navigation__toggle:focus-visible {
  outline: 2px solid #5496bd;
}
.navigation__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
  transition: 0.5s;
}
.navigation__wrapper--opened {
  transform: translateY(0);
}
.navigation__wrapper.navigation__wrapper--nojs {
  position: static;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .navigation__wrapper {
    display: flex;
    flex-direction: row;
    position: static;
    transform: none;
    justify-content: space-between;
  }
}
.navigation__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    background-color: transparent;
    align-items: center;
    justify-items: center;
  }
}
.navigation__item {
  width: 100vw;
  text-align: center;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
